.gallery {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

*, :before, :after {
  box-sizing: border-box;
  outline: none;
}

a {
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  border-radius: 10px;
  justify-content: center;
  align-items: flex-start;
  width: 395px;
  height: 290px;
  padding: 20px;
  transition: all .5s cubic-bezier(.25, .8, .25, 1);
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 14px 28px #00000040, 0 10px 10px #00000038;
}

a:before, a:after {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

a:before {
  content: "";
  z-index: 0;
  background-color: #0000001a;
}

a:after {
  content: "";
  justify-content: center;
  align-items: center;
  transition: all .5s cubic-bezier(.25, .8, .25, 1);
  display: flex;
  overflow: hidden;
}

a:hover {
  background-size: 175%;
}

a:hover:after {
  content: "♣︎";
  color: #fff;
  z-index: 2;
  background-color: #00fede;
  font-size: 72px;
}

a {
  margin: 0;
}

.svg-icon {
  fill: #00fede;
}

.photo-card {
  color: #00fede;
  flex-direction: column;
  align-items: center;
  margin: 20px;
  display: flex;
}

.info {
  flex-wrap: wrap;
  width: 395px;
  padding: 20px;
  display: flex;
}

.info-item {
  margin-right: 40px;
}

